import React, { useEffect, useState } from "react";
import { auth, firestore } from "../firebase"; // Certifique-se de que esses imports esto corretos
import { doc, getDoc } from "firebase/firestore";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

const AdminPanel = () => {
  const [loading, setLoading] = useState(true);
  const [isAdmin, setIsAdmin] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    // Funo para verificar o papel do usurio no Firestore
    const checkUserRole = async () => {
      const user = auth.currentUser;
      if (user) {
        try {
          const userDoc = doc(firestore, 'users', user.uid);
          const docSnap = await getDoc(userDoc);

          if (docSnap.exists()) {
            const userData = docSnap.data();
            if (userData.role === "admin") {
              setIsAdmin(true);
            } else {
              toast.error("Acesso negado. Somente administradores podem acessar esta pgina.");
              navigate("/"); // Redireciona para a pgina inicial
            }
          } else {
            console.error("Documento do usurio no encontrado.");
            navigate("/login");
          }
        } catch (error) {
          console.error("Erro ao verificar o papel do usurio:", error);
          navigate("/login");
        } finally {
          setLoading(false);
        }
      } else {
        navigate("/login");
      }
    };

    checkUserRole();
  }, [navigate]);

  if (loading) {
    return <div>Carregando...</div>; // Tela de carregamento enquanto verifica o papel
  }

  if (!isAdmin) {
    return null; // No renderiza nada se no for administrador
  }

  return (
    <div className="mt-24 text-center">
      <h1>Admin Panel</h1>
      <p>Bem-vindo ao painel do administrador.</p>
      {/* Aqui voc pode adicionar os formulrios para adicionar produtos, alterar preos, etc */}
      <button onClick={() => alert("Funo de adicionar produtos")}>
        Adicionar Produto
      </button>
      <button onClick={() => alert("Funo de alterar preos")}>
        Alterar Preos
      </button>
      {/* Outros botes e funcionalidades de administrador */}
    </div>
  );
};

export default AdminPanel;
